.PageSpeedmate {
    padding: 30px 20px 30px;
    width: 100%;
    height: 100%;
    background-color: #f9fafc;
    // background-color: rgba(0, 0, 0, 0.9);
    position: fixed;
    z-index: 200;
    // display: flex;
    align-items: center;
    justify-content: center;
    top: 0;
    left: 0;
    .box-sentense {
        border: 1px solid #e5e8eb;
        background-color: #fff;
        border-radius: 8px;
        overflow: hidden;
        width: 100%;
        margin-bottom: 20px;
        &-in {
            padding: 10px;
            .en {
                font-size: 14px;
                margin-bottom: 5px;
                opacity: 0.5;
            }
            .ko {
                font-size: 14px;
                margin-bottom: 5px;
                opacity: 0.5;
            }
        }
        .box-state {
            padding: 10px;
            background-color: #f6f8f9;
            display: flex;
            border-top: 1px solid #edf0f2;
            .dp-state {
                width: 50%;
                text-align: center;
            }
        }
    }
    .box-btn-speed {
        display: flex;
        justify-content: center;
        .btn-speed {
            margin: 10px;
            width: 90px;
            height: 90px;
            border: 1px solid #f0f2f3;
            border-radius: 50%;
            font-weight: bold;
            line-height: 18px;
            color: #828383;
            background-color: #fff;
            &:hover {
                background-color: rgb(243, 243, 243);
                color: #000;
            }
            &:active {
                background-color: rgb(233, 233, 233);
                color: rgb(68, 68, 68);
            }
        }
    }

    .btn-close {
        font-size: 25px;
        float: right;
        padding: 10px;
        margin: 10px;
        margin-right: 0;
    }
    .pill-scroll {
        width: 100vw;
        overflow-x: scroll;
        overflow-y: hidden;
        .pills {
            display: flex;
            width: 100px * 5;
            align-items: center;
            .pill-item {
                width: 100px;
                background-color: #e1e1e1;
                margin: 5px;
                padding: 5px;
                border-radius: 5px;
                text-align: center;
                &.active {
                    background-color: #ffffff;
                }
                &.done {
                    opacity: 0.5;
                }
            }
            .pill-arrow {
                &.done {
                    opacity: 0.5;
                }
            }
        }
    }
}

