.equalizer {
    width: 15px;
    height: 10px;
    border: 0;
    position: relative;
    transition: opacity 0.5s ease;
    display: inline-block;
    span {
        display: inline-block;
        width: 3px;
        background-color: $color-brand-green2;
        position: absolute;
        bottom: 0;
    }

    @keyframes shorteq {
        0% {
            height: 10px;
        }
        50% {
            height: 5px;
        }
        100% {
            height: 10px;
        }
    }
    @-webkit-keyframes shorteq {
        0% {
            height: 10px;
        }
        50% {
            height: 5px;
        }
        100% {
            height: 10px;
        }
    }

    @keyframes talleq {
        0% {
            height: 12px;
        }
        50% {
            height: 8px;
        }
        100% {
            height: 12px;
        }
    }
    @-webkit-keyframes talleq {
        0% {
            height: 12px;
        }
        50% {
            height: 5px;
        }
        100% {
            height: 12px;
        }
    }

    .eq1 {
        height: 13px;
        left: 0;
        animation-name: shorteq;
        animation-duration: 0.5s;
        animation-iteration-count: infinite;
        animation-delay: 0s;
        -webkit-animation-name: shorteq;
        -webkit-animation-duration: 0.5s;
        -webkit-animation-iteration-count: infinite;
        animation-delay: 0s;
    }

    .eq2 {
        height: 15px;
        left: 6px;
        animation-name: talleq;
        animation-duration: 0.5s;
        animation-iteration-count: infinite;
        animation-delay: 0.17s;
        -webkit-animation-name: talleq;
        -webkit-animation-duration: 0.5s;
        -webkit-animation-iteration-count: infinite;
        animation-delay: 0.17s;
    }

    .eq3 {
        height: 13px;
        left: 12px;
        animation-name: shorteq;
        animation-duration: 0.5s;
        animation-iteration-count: infinite;
        animation-delay: 0.34s;
        -webkit-animation-name: shorteq;
        -webkit-animation-duration: 0.5s;
        -webkit-animation-iteration-count: infinite;
        animation-delay: 0.34s;
    }
}

