.PageCSSHamburger{

    .hamburger{
        $color-bar: #000;
        $wh: 40px;
        $bar-border-width: 5px;
        $bar-border-radius: 2px;
        $bar-margin-bottom: 10px;

        width: auto;
        margin: 0 auto;
        position: relative;
        display: inline-block;
        width: $wh;
        input{
            display: block;
            width: $wh;
            height: $wh;
            position: absolute;
            cursor: pointer;
            opacity: 0;
            z-index: 1;
            &:checked ~ span
            {
                background-color: $color-bar;
              &:nth-last-child(3)
              {
                transform: translateY(15px) rotateZ(45deg);
              }
              &:nth-last-child(1)
              {
                transform: translateY(-15px) rotateZ(-45deg);
              }
              &:nth-last-child(2)
              {
                width: 0;
                opacity: 0;
              }
            }
        }
        span{
            display: block;
            height: $bar-border-width;
            border-radius: $bar-border-radius;
            width: $wh;
            background-color: $color-bar;
            margin-bottom: $bar-margin-bottom;
            transition: all .4s cubic-bezier(0.77,0.2,0.05,1.0);
            &:first-child{
                transform-origin: 0% 0%;
            }
            &:nth-last-child(2){
                transform-origin: 0% 100%;
            }
            &:nth-last-child(1){
                margin-bottom: 0;
            }
        }
    }


}
